<template>
	<nav>
        <a v-bind:class="{'cur': tapId==1}" @click="changeTap(1,'/')"><i></i>推荐</a>
        <a v-bind:class="{'cur': tapId==2}" @click="changeTap(2,'invest')"><i></i>理财</a>
        <a v-bind:class="{'cur': tapId==3}" @click="changeTap(3,'fund')"><i></i>资产</a>
        <a v-bind:class="{'cur': tapId==4}" @click="changeTap(4,'account')"><i></i>我</a>
    </nav>
</template>

<script type="text/javascript">
	export default {
		props: ['tapId'],
		data() {
			return {

			}
		},
		computed: {

		},
		methods: {
			// destination目的地
			changeTap(tapId,des) {
				this.$router.push(des);
			}
		}
	}
</script>



